.Persik {
  display: block;
  width: 100%;
  max-width: 100%;
  margin: 20px auto;
}
.noselect {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.menu {
    width: 200px;
    height: 150px;
    overflow: hidden;

}

.menu img {
    width: 115px;
    position: fixed;
    top:97%;
    right: -15px;
    height: 115px;
    margin: -75px 0 0 -100px;
}
.box {
    width:100px;
    min-height:100px;

    margin: 0 auto;
    display: inline-block;
    vertical-align: middle;
}